.header {
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 50px;
  display: flex;
}

.logo {
  margin: 0;
  padding: 0;
  text-decoration: none;
  display: inline-block;
}

.logo__images {
  border: 0;
  width: 40px;
  height: 40px;
  margin: 0;
  padding: 0;
  display: block;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.is-active:after:hover {
  color: #00acdc;
}

.nav__list {
  gap: 20px;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav__item {
  margin: 0;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.nav__link.is-active, .nav__link:hover {
  color: #00acdc;
}

.card {
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 17px;
  transition: transform .3s;
  display: flex;
}

.card__img {
  align-self: center;
  width: 160px;
  height: 134px;
}

.card__title {
  color: #060b35;
  padding: 40px 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__subtitle {
  color: #616070;
  font-size: 10px;
  line-height: 14px;
}

.card__btn {
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  background: #00acdc;
  border: none;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
}

.card__btn:hover {
  color: #00acdc;
  background: #fff;
  border: 1px solid #00acdc;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.grade {
  justify-content: space-between;
  align-items: center;
  padding: 16px 0 24px;
  display: flex;
}

.grade__text {
  color: #060b35;
  font-size: 10px;
  line-height: 14px;
}

.stars {
  display: flex;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.price {
  justify-content: space-between;
  padding-bottom: 16px;
  display: flex;
}

.price__title {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.price__subtitle {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.cards {
  grid-template-columns: repeat(4, 200px);
  justify-content: center;
  gap: 45px 48px;
  max-width: 1024px;
  padding: 50px 40px;
  display: grid;
}

@media screen and (width >= 768px) and (width <= 1023px) {
  .cards {
    grid-template-columns: repeat(3, 200px);
  }
}

@media screen and (width >= 488px) and (width <= 767px) {
  .cards {
    grid-template-columns: repeat(2, 200px);
  }
}

@media screen and (width <= 487px) {
  .cards {
    grid-template-columns: repeat(1, 200px);
  }
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

h2, p {
  margin: 0;
}

.main {
  justify-content: center;
  display: flex;
}

